
h1 {
  margin: 0;
  font-size: 3rem;
  font-weight: normal;
}
.main {
  max-width: 650px;
  margin: 15vh auto 20px;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  font-size: 14px;
}
.code-wrap {
  text-align: left;
  background-color: #f8f8f8;
  padding: 1.2em 1.4em;
  line-height: 1.5em;
  margin: 60px 0 0;
  overflow: auto;
}
.code {
  margin: 0;
  padding: 0;
}
footer {
  margin-top: 40px;
  line-height: 2;
}
a {
  color: #42b983;
}



